@import "../../../styles/vars";
@import "../../../styles/mixins";


.joinForm {
  margin: 202px auto 0;

  @include xxlScreenMedia {
    margin-top: 180px;
  }

  @include xlScreenMedia {
    margin-top: 150px;
  }

  @include lgScreenMedia {
    margin-top: 120px;
  }

  @include mdScreenMedia {
    margin-top: 100px;
  }

  @include smScreenMedia {
    margin-top: 78px;
  }

  &__fileInput {
    width: 0;
    visibility: hidden;
  }

  &__fileLabel {
    width: 100%;
    display: block;
    @include flexCol(flex-start);
    cursor: pointer;
  }

  &__fileAttachIcon {
    @include svg(9px,$color-white,18px);
    margin-right: 7px;
  }

  &__profession {
    display: block;
    color: #FFFFFF00!important;

    &_active {
      color: $color-white!important;
    }
  }

}