@import "../../../styles/vars";
@import "../../../styles/mixins";

.formDef {

  &__form {
    max-width: 1369px;
    padding: 150px 40px 75px;
    border-radius: 40px;
    border: 2px solid $color-white;
    margin-left: auto;
    margin-right: auto;

    @include xlScreenMedia {
      padding-top: 120px;
      padding-bottom: 55px;
    }

    @include lgScreenMedia {
      padding-top: 80px;
      padding-bottom: 45px;
      border-radius: 30px;
    }

    @include mdScreenMedia {
      padding: 50px 20px 35px;
      border-radius: 20px;
    }

    @include smScreenMedia {
      border-radius: 16px;
      padding: 40px 12px 15px;
      border-width: 1px;
    }
  }

  &__main {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 80px 86px;

    @include lgScreenMedia {
      gap: 50px 56px;
    }

    @include mdScreenMedia {
      gap: 38px 36px;
    }

    @include smScreenMedia {
      grid-template-columns: 1fr;
    }
  }

  &__btn {
    display: block;
    margin: 40px auto 20px;

    @include smScreenMedia {
      margin-top: 30px;
      margin-bottom: 10px;
    }
  }

  &__errorText {
    text-align: center;
    opacity: 0;
    transition: $transition;

    &_active {
      opacity: 1;
    }

    &_error {
      color: $color-dark-red;
    }

    &_success {
      color: $color-green;
    }
  }
}
